	module.exports = {"data":{"markdownRemark":{"html":"<h1>Selectors</h1>\n<p>Selectors are CSS rules to target HTML elements to apply styles. Tag names, class names, id, and attributes are some of the hooks used as selectors.</p>\n<h2>Selector Syntax</h2>\n<p>Selectors arranged in a specific sequence would build up to a rule to target elements. An example, </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token comment\" spellcheck=\"true\">/* selects anchor tags */</span>\n<span class=\"token selector\">a</span> <span class=\"token punctuation\">{</span> \n    <span class=\"token property\">color</span><span class=\"token punctuation\">:</span> orange<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\" spellcheck=\"true\">/* selects elements with hero class */</span>\n<span class=\"token selector\">.hero</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">text-align</span><span class=\"token punctuation\">:</span> center<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h2>Type of Selectors</h2>\n<table>\n<thead>\n<tr>\n<th>Type</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Type selectors</td>\n<td>Tag names are used to select elements such as \n<code>h1</code>\n or \n<code>a</code>\n.</td>\n</tr>\n<tr>\n<td>Universal Selector</td>\n<td>Selector that applies to all elements. \n<code>div *</code>\n matches all elements within div elements.</td>\n</tr>\n<tr>\n<td>Attribute Selector</td>\n<td>Selectors that target elements based on their attributes \n[\nand optionally, their values\n]\n. \n<code>h1[title]</code>\n selects \n<code>h1</code>\n elements with \n<code>title</code>\n attribute.</td>\n</tr>\n<tr>\n<td>Class Selector</td>\n<td>Selector that targets elements using their class names.</td>\n</tr>\n<tr>\n<td>ID Selector</td>\n<td>Selector that uses ID to target elements. \n<code>#logo</code>\n selects the element with \n<code>logo</code>\n as ID.</td>\n</tr>\n<tr>\n<td>Pseudo-class Selector</td>\n<td>Special selectors that target elements based on their state. \n<code>a:hover</code>\n selector applies style when pointer hovers over links.</td>\n</tr>\n</tbody>\n</table>\n<h2>Selector Combinators</h2>\n<table>\n<thead>\n<tr>\n<th>Combinator</th>\n<th>Purpose</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>white space</code></td>\n<td>Descendant combinator. \n<code>.nav li</code>\n selects all \n<code>li</code>\n children within the class \n<code>.nav</code>\n, including nested \n<code>li</code>\n elements.</td>\n</tr>\n<tr>\n<td><code>></code></td>\n<td>Child combinator. \n<code>.menu > li</code>\n selects all li that are direct children of elements with \n<code>.menu</code>\n class.</td>\n</tr>\n<tr>\n<td><code>+</code></td>\n<td>Adjacent sibling combinator. \n<code>.logo + h1</code>\n targets \n<code>h1</code>\n that is an immediate sibling to \n<code>.logo</code>\n class.</td>\n</tr>\n<tr>\n<td><code>~</code></td>\n<td>General sibling combinator. \n<code>header ~ div</code>\n targets \n<code>div</code>\n elements that are siblings to \n<code>header</code>\n elements.</td>\n</tr>\n</tbody>\n</table>\n<p>This section details all of these electors.</p>\n<h4>More Information:</h4>\n<p>You can learn more about selectors on these resources:</p>\n<ul>\n<li><a href=\"https://www.w3.org/TR/css3-selectors\">Official CSS3 Selectors specification</a></li>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Learn/CSS/Introduction_to_CSS/Selectors\">Selectors page on Mozilla Developer Network</a></li>\n<li><a href=\"https://guide.freecodecamp.org/css/tutorials/css-selectors-cheat-sheet\">CSS Selectors Cheat Sheet on FreeCodeCamp Guides</a></li>\n</ul>","fields":{"slug":"/css/selectors/"},"frontmatter":{"title":"Selectors"}}},"pathContext":{"slug":"/css/selectors/"}}

